import React from 'react';
import './Navbar.css';

const Navbar = () => {
  return (
    <div>
      <nav>
        <span>
          <a href='#home'>Home</a>
        </span>
        <span>
          <a href='#about'>About</a>
        </span>
        <span>
          <a href='#Resume'>Resume</a>
        </span>
        <span>
          <a href='#portfolio'>portfolio</a>
        </span>
        <span>
          <a href='#contact'>Contact</a>
        </span>
        <span className='nav-button'>
          <i className="fas fa-bars fa-2x"></i>
        </span>
      </nav>
    </div>
  )
}

export default Navbar;