import React from 'react';
import './Contact.css';

const Contact = () => {
  return (
    <div id='contact' className='section'>
      <h1>Contact Me</h1>
      <div className='title-line'></div>
      <div className='contact-info'>
        <div>
          <i class="far fa-envelope fa-2x"></i>
          <a href='mailto:jake@example.com' target='_blank' rel='noopener noreferrer'>jake@example.com</a>
        </div>
        <div>
          <i class="fas fa-phone fa-2x"></i>
          <p>555-555-5555</p>
        </div>
        <div>
          <i class="fab fa-github fa-2x"></i>
          <a href='https://github.com/jburto94' target='_blank' rel='noopener noreferrer'>https://github.com/jburto94</a>
        </div>
        <div>
          <i class="fab fa-codepen fa-2x"></i>
          <a href='https://codepen.io/jburton94/' target='_blank' rel='noopener noreferrer'>https://codepen.io/jburton94/</a>
        </div>
      </div>
    </div>
  )
}

export default Contact;